<template>

    <div class="container">

        <h1>Tabla de Estudiantes</h1>
        <table>
            <thead>
                <tr>
                   <th>Codigo</th>
                   <th>Nombre</th>
                   <th>Apellido</th>
                   <th>Edad</th>     
                </tr>
            </thead>
            <tbody>
                <tr v-for="estudiante in estudiantes" :key="estudiante.codigo">
                    <td>{{estudiante.codigo}}</td>
                    <td>{{estudiante.nombre}}</td>
                    <td>{{estudiante.apellido}}</td>
                    <td>{{estudiante.edad}}</td>
                </tr>
               

            </tbody>
        </table>

    </div>

</template>

<script>

import axios from "axios";

export default{

    data(){

        return{

            estudiantes:[],
        };

    },

    methods:{

        obtenerEstudiantes(){

            axios
            .get("https://colegioxyz-production-0127.up.railway.app/api/estudiantes/listar")
            .then((response)=>{

                this.estudiantes=response.data;

            })
            .catch((error)=>{

                console.error("error al listar los estudiantes",error);
            });


        },


    },

    mounted(){

        this.obtenerEstudiantes();
    },

};

</script>