<template>
    <div class="container">
        <h1>Formulario de Estudiantes</h1>

        <form id="student-form" @submit.prevent="guardar">

            <div class="form-group">
                <label for="codigo">Codigo:</label>
                <input type="text" id="codigo" name="codigo" required v-model="codigo">
            </div>

            <div class="form-group">
                <label for="nombre">Nombre:</label>
                <input type="text" id="nombre" name="nombre" required v-model="nombre">
            </div>

            <div class="form-group">
                <label for="apellido">Apellido:</label>
                <input type="text" id="apellido" name="apellido" required v-model="apellido">
            </div>

            <div class="form-group">
                <label for="edad">Edad:</label>
                <input type="number" id="edad" name="edad" required v-model="edad">
            </div>

            <button type="submit" id="guardar" name="guardar">Guardar</button><br>
            <button type="button" id="eliminar" name="eliminar" @click="eliminar">Eliminar</button><br>
            <button type="button" id="actualizar" name="actualizar" @click="actualizar">Actualizar</button><br>
            <button type="button" id="consultar" name="consultar" @click="consultar">Consultar</button><br>
       
        </form>
    </div>

</template>

<script>

import axios from "axios";

export default {

data() {

    return{
        codigo:"",
        nombre:"",
        apellido:"",
        edad:"",
    };

},
    methods:{

        guardar(){

            axios

                .post("https://colegioxyz-production-0127.up.railway.app/api/estudiantes",{

                    codigo:this.codigo,
                    nombre:this.nombre,
                    apellido:this.apellido,
                    edad:this.edad,
                })

                .then((response)=>{
                    console.log("estudiante registrado con exito",response.data);
                    alert("exito");
                    this.codigo="";
                    this.nombre="";
                    this.apellido="";
                    this.edad="";
                    this.$emit("actualizar-tabla")

                })

                .catch((error)=>{

                    console.error("error al registrar el estudiante", error);

                });

        },

        consultar(){

            axios
            .get("https://colegioxyz-production-0127.up.railway.app/api/estudiantes"+this.codigo)
            .then((response)=>{

                this.nombre=response.data.nombre;
                this.apellido=response.data.apellido;
                this.edad=response.data.edad;
            })
            .catch((error)=>{

                console.error("error al consultar un estudiante",error);

            });

        },

        actualizar(){

            axios
            .put("https://colegioxyz-production-0127.up.railway.app/api/estudiantes/actualizar/"+this.codigo,{

                codigo:this.codigo,
                nombre:this.nombre,
                apellido:this.apellido,
                edad:this.edad,

            })
            .then((response)=>{

                alert("estudiante actualizado con exito")
                console.log("estudiante actualizado con exito",response.data);
                this.codigo="";
                this.nombre="";
                this.apellido="";
                this.edad="";
            })
            .catch((error)=>{

                console.error("error al actualizar el estudiante",error);
            });

        },

        eliminar(){

            axios
            .delete("https://colegioxyz-production-0127.up.railway.app/api/estudiantes/"+this.codigo)
            .then(()=>{

                alert("estudiante eliminado")
                console.log("estudiante eliminado con exito");
                
                this.codigo="";
                this.nombre="";
                this.apellido="";
                this.edad="";

            })
            .catch((error)=>{

                console.error("fallo al elminar estudiante",error)
            });

        },


    },


};





</script>